
"use client";

import React, { useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { payrollRecords as initialPayroll, getEmployeeName, employees } from '@/lib/data';
import type { Payroll } from '@/lib/types';
import { useUser } from '@/contexts/user-provider';
import { Download, FileText } from 'lucide-react';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

export default function PayrollPage() {
  const { user } = useUser();
  const [payroll, setPayroll] = useState<Payroll[]>(initialPayroll);

  if (!user) return null;

  const canGeneratePayroll = user.role === 'Admin' || user.role === 'HR Staff';
  const canViewOwnSlip = user.role === 'Employee' || user.role === 'Manager' || user.role === 'Admin' || user.role === 'HR Staff';

  const userPayroll = payroll.filter(p => p.employeeId === user.id);

  const generatePayroll = () => {
    // In a real app, this would be a complex process.
    // Here we'll just add a dummy record for a random employee.
    const randomEmployee = employees.find(e => e.role === 'Employee');
    if (randomEmployee) {
        const newPayroll: Payroll = {
            id: `p${payroll.length + 1}`,
            employeeId: randomEmployee.id,
            payPeriod: '2024-08',
            grossSalary: 5200,
            deductions: 1100,
            netSalary: 4100
        };
        setPayroll([...payroll, newPayroll]);
    }
  };
  
  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(amount);
  };

  if (!canGeneratePayroll && !canViewOwnSlip) {
      return (
        <div className="flex items-center justify-center h-full">
            <Card className="w-full max-w-md">
                <CardHeader>
                    <CardTitle>Access Denied</CardTitle>
                </CardHeader>
                <CardContent>
                    <p>You do not have permission to view this page. Please contact an administrator.</p>
                </CardContent>
            </Card>
        </div>
      );
  }

  return (
    <div className="space-y-8">
       <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold">Payroll</h1>
        {canGeneratePayroll && (
            <AlertDialog>
                <AlertDialogTrigger asChild>
                    <Button>Generate Payroll</Button>
                </AlertDialogTrigger>
                <AlertDialogContent>
                    <AlertDialogHeader>
                    <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                    <AlertDialogDescription>
                        This will generate the payroll for the current period for all eligible employees. This action cannot be undone.
                    </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                    <AlertDialogCancel>Cancel</AlertDialogCancel>
                    <AlertDialogAction onClick={generatePayroll}>Generate</AlertDialogAction>
                    </AlertDialogFooter>
                </AlertDialogContent>
            </AlertDialog>
        )}
      </div>

        {canGeneratePayroll && (
            <Card>
                <CardHeader>
                <CardTitle>Payroll Reports</CardTitle>
                </CardHeader>
                <CardContent>
                <Table>
                    <TableHeader>
                    <TableRow>
                        <TableHead>Employee</TableHead>
                        <TableHead>Pay Period</TableHead>
                        <TableHead>Gross Salary</TableHead>
                        <TableHead>Deductions</TableHead>
                        <TableHead>Net Salary</TableHead>
                        <TableHead className="text-right">Actions</TableHead>
                    </TableRow>
                    </TableHeader>
                    <TableBody>
                    {payroll.map((record) => (
                        <TableRow key={record.id}>
                        <TableCell>{getEmployeeName(record.employeeId)}</TableCell>
                        <TableCell>{record.payPeriod}</TableCell>
                        <TableCell>{formatCurrency(record.grossSalary)}</TableCell>
                        <TableCell>{formatCurrency(record.deductions)}</TableCell>
                        <TableCell>{formatCurrency(record.netSalary)}</TableCell>
                        <TableCell className="text-right">
                            <Button variant="outline" size="sm">
                                <Download className="mr-2 h-4 w-4" /> Download
                            </Button>
                        </TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
                </CardContent>
            </Card>
        )}

        {canViewOwnSlip && (
             <Card>
                <CardHeader>
                    <CardTitle>My Salary Slips</CardTitle>
                </CardHeader>
                <CardContent>
                    {userPayroll.length > 0 ? (
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Pay Period</TableHead>
                                    <TableHead>Gross Salary</TableHead>
                                    <TableHead>Deductions</TableHead>
                                    <TableHead>Net Salary</TableHead>
                                    <TableHead className="text-right">Actions</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                            {userPayroll.map((record) => (
                                <TableRow key={record.id}>
                                    <TableCell>{record.payPeriod}</TableCell>
                                    <TableCell>{formatCurrency(record.grossSalary)}</TableCell>
                                    <TableCell>{formatCurrency(record.deductions)}</TableCell>
                                    <TableCell>{formatCurrency(record.netSalary)}</TableCell>
                                    <TableCell className="text-right">
                                        <Button variant="outline" size="sm">
                                            <FileText className="mr-2 h-4 w-4" /> View Slip
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                            </TableBody>
                        </Table>
                    ) : (
                        <p className="text-muted-foreground text-center">No salary slips available for you yet.</p>
                    )}
                </CardContent>
             </Card>
        )}
    </div>
  );
}
